.blog-post-block {
  h2 {
    //padding-top: 5rem;
    //padding-bottom: 2.5rem;
  }
  hr {
    
    width: 5%;
    border-top: 2px solid $color-nax;
   }

  main {
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      grid-template-rows: 1fr;
      grid-column-gap: 25px;
      grid-row-gap: 25px;
    > .subtitle2 {
      font-size: 1.1em;
    }
    > .abstract {
      font-size: 1em;
    }
    > a {
      text-decoration: none;
      h3 {
        text-align: left;
      }
    }
    img {
      max-width: 100%;      
      //height: auto;
      width: 100%;
    }
    .share-buttons {
      > * {
        display: inline-flex;
      }
    }
  }
}

.wp-block-column:not(:first-child) {
  margin-left: 0;
  margin-right: 8rem;
}

.wp-block-button {
  > a {
    color: $color-light !important;
  }
}
